.header {
  background: center, url(/static/media/header-img.9aa0862f.jpg), no-repeat,
    linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  /* filter: brightness(70%); */
  background-size: cover;
  height: 373px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header-title > h1 {
  color: white;
  -webkit-filter: brightness(100%);
          filter: brightness(100%);
  text-shadow: 3px 3px 5px rgb(61, 59, 59);
  font-family: "Amatic";
  font-size: 4rem;
}

.header-title {
  height: 100px;
  width: 100p;
  width: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.landing-buttons {
  height: 125px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.about-us {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  text-align: justify;
  margin: 10px 0;
  height: 17rem;
}

.about-us > p {
  padding: 5px 20px;
}

html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  font-size: 10px;
  font-family: "Roboto";
  min-height: 100vh;
}

.buttons {
  padding: 10px 20px;
  border-radius: 10px;
  box-shadow: 3px 3px 5px rgba(163, 156, 156, 0.76);
  width: 112px;
  height: 60px;
  font-size: 1.6rem;
}

p {
  font-size: 1.6rem;
}

h1 {
  font-size: 10rem;
}

